import React, { useState, useEffect, useRef } from 'react';
import { db } from '../firebase';
import { 
  collection, 
  addDoc, 
  query, 
  orderBy, 
  limit, 
  serverTimestamp,
  onSnapshot 
} from 'firebase/firestore';

function ChatPage() {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [username, setUsername] = useState('');
  const messagesEndRef = useRef(null);

  // 
  useEffect(() => {
    // messages
    const messagesRef = collection(db, 'messages');
    
    // 50
    const q = query(
      messagesRef,
      orderBy('timestamp', 'asc'),
      limit(50)
    );

    // 
    const unsubscribe = onSnapshot(q, (snapshot) => {
      const messageList = [];
      snapshot.forEach((doc) => {
        const messageData = doc.data();
        messageList.push({
          id: doc.id,
          text: messageData.text,
          sender: messageData.user === username ? 'user' : 'other',
          username: messageData.user,
          timestamp: messageData.timestamp?.toDate() || new Date()
        });
      });
      setMessages(messageList);
    });

    // 
    return () => unsubscribe();
  }, [username]);

  // 
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // 
  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (newMessage.trim() === '') return;

    try {
      // Firestore
      await addDoc(collection(db, 'messages'), {
        text: newMessage,
        user: username,
        timestamp: serverTimestamp()
      });

      // 
      setNewMessage('');
    } catch (error) {
      console.error(':', error);
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="card">
          <div className="card-header">
            <h2>Firebase</h2>
          </div>
          <div className="card-body">
            <div className="mb-3">
              <label htmlFor="username" className="form-label"></label>
              <input
                type="text"
                className="form-control"
                id="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
            </div>
            <div 
              className="chat-messages p-3 mb-3 bg-light" 
              style={{ height: '400px', overflowY: 'auto' }}
            >
              {messages.map((message) => (
                <div 
                  key={message.id} 
                  className={`mb-2 d-flex ${message.sender === 'user' ? 'justify-content-end' : 'justify-content-start'}`}
                >
                  <div 
                    className={`p-2 rounded-3 ${
                      message.sender === 'user' 
                        ? 'bg-primary text-white' 
                        : 'bg-light border'
                    }`}
                    style={{ maxWidth: '70%' }}
                  >
                    {message.sender !== 'user' && (
                      <div className="fw-bold small">{message.username}</div>
                    )}
                    <div>{message.text}</div>
                    <div className="text-end small opacity-75">
                      {message.timestamp.toLocaleTimeString()}
                    </div>
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
            <form onSubmit={handleSendMessage}>
              <div className="input-group">
                <input
                  type="text"
                  className="form-control"
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="..."
                />
                <button 
                  type="submit" 
                  className="btn btn-primary"
                >
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ChatPage;